// src/app/page.tsx
"use client";
import React, { useState } from "react";
import { AnimatePresence, motion } from "framer-motion";
import { DiagnosticFlow } from "@/components/system/DiagnosticFlow";

export default function Page() {
  const [started, setStarted] = useState(false);

  return (
    <main className="h-[100svh] bg-[#080808] text-white flex flex-col items-center justify-center p-6 font-mono">
      {!started ? (
        <motion.div 
          initial={{ opacity: 0 }} animate={{ opacity: 1 }}
          className="max-w-xl text-center"
        >
          <h1 className="text-xs tracking-[0.3em] text-zinc-500 uppercase mb-8">System Initialized</h1>
          <p className="text-xl mb-10 text-zinc-300">Most ideas are just noise. <br/> Is yours worth building?</p>
          <button 
            onClick={() => setStarted(true)}
            className="border border-white/20 px-8 py-4 hover:bg-white hover:text-black transition-all text-xs tracking-widest"
          >
            RUN DIAGNOSTIC
          </button>
        </motion.div>
      ) : (
        <DiagnosticFlow />
      )}
    </main>
  );
}