// src/components/system/DiagnosticFlow.tsx
"use client";
import React, { useState } from "react";
import { motion } from "framer-motion";

const STEPS = [
  "What is the core intent?",
  "Who loses if this fails?",
  "Why does this need to be different?"
];

export function DiagnosticFlow() {
  const [step, setStep] = useState(0);
  const [loading, setLoading] = useState(false);

  const next = () => {
    if (step < STEPS.length - 1) setStep(step + 1);
    else setLoading(true);
  };

  if (loading) return <ResultView />;

  return (
    <div className="w-full max-w-2xl">
      <div className="text-[10px] text-zinc-500 mb-2">PHASE_0{step + 1}</div>
      <h2 className="text-2xl mb-8 tracking-tight">{STEPS[step]}</h2>
      <textarea 
        autoFocus
        className="w-full bg-white/5 border border-white/10 p-6 outline-none focus:border-white/30 h-32 resize-none"
        placeholder="Type honestly..."
      />
      <button onClick={next} className="mt-4 text-[10px] tracking-widest opacity-50 hover:opacity-100">
        NEXT_STEP â†’
      </button>
    </div>
  );
}

function ResultView() {
  return (
    <motion.div initial={{ opacity: 0 }} animate={{ opacity: 1 }} className="max-w-md text-center">
      <div className="inline-block px-2 py-1 bg-red-500/10 text-red-500 text-[10px] mb-4">CRITICAL_MATCH</div>
      <h2 className="text-xl mb-4">You aren't looking for a website.</h2>
      <p className="text-zinc-400 text-sm leading-relaxed mb-8">
        You're building a pattern-breaker. That requires a level of cognitive friction most designers try to remove. I build specifically for this level of intent.
      </p>
      <a 
        href="mailto:hello@tatedonohoe.au" 
        className="block w-full border border-white py-4 bg-white text-black font-bold hover:invert transition-all"
      >
        SEND THE DIAGNOSTIC TO ME
      </a>
    </motion.div>
  );
}